/* style.css 開始 */

/* 正常網頁版 Layout when screen width >= 1200px */

* {
  box-sizing:border-box;
}

.wrapper {
  height: 100%;
}

.main-header {
  display: flex;
  width: 100%;
  background-color: #EEEEEE;
}

.title-logo {
  padding: 10px 30px;
  font-family: 'Times New Roman', Times, serif;
  font-size: 14px;
}

nav {
  display: flex;
  padding: 10px 15px;
  position: absolute;
  top: 0;
  right: 0;
  font-family: 'Times New Roman', Times, serif;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

a:hover {
  color: rgb(153, 135, 246);
}

li {
  padding: 0 20px;
  font-size: 14px;
}

.hamburger-menu {
  display: none;
}

.main-nav {
  display: flex;
  list-style-type: none;
}

.message {
  width: 100%;
  height: 20vh;
  flex:1;
  background-color: #CDE;
}

h2 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: 'Times New Roman', Times, serif;
}

h2.title {
  margin:20px 0;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 20px;
  flex-wrap: wrap;
  justify-content: center; 
}

.item {
  width: calc(40% - 10px); 
  height: 150px;
  display: flex;
  justify-content: center;
  box-sizing: border-box; 
  flex-wrap: wrap;
  flex-basis: auto;
  margin-bottom: 12px;
  margin-left: 12px;
  padding-top: 12px;
  font-family: 'Times New Roman', Times, serif;
  background-color: #89A;
}

.cta-container {
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.cta-button {
  display: flex;
  align-items: center;
  margin: auto;
  margin-bottom: 1vh;
  padding: 3vh 4vh;
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  background-color:#DAEBFC; 
  border: 0;
}

/* Layout when screen width >= 500px and < 800px */

@media screen and (min-width:500px) and (max-width:800px)  {
      .main-nav {
          display: none;
      }
      
      .hamburger-container {
          position: absolute;
          top: 20px; 
          right: 60px; 
          z-index: 0;
          position: absolute;
      }

      .hamburger-menu {
          width: 30px;
          height: 20px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          cursor: pointer;
          position: absolute;
      }
      

      .line {
          width: 100%;
          height: 3px;
          background-color: #6c6c6c; 
      }

      .message {
          height: 30vh;
      }

      .hamburger-menu-container{
          background-color: #ffffff;
          width: 40vh;
          height: 100vh;
      }
      
      .menu-list{
          list-style: none;
          position: relative;
          left: -60px;
          top: -40px;
      }
      
      .menu-list li{
          margin: 5vh 0 ;
          font-family: 'Times New Roman', Times, serif;
          font-size: 16px;
      }
      
      .x-button{
          position: absolute;
          right: 15px;
          top: 10px;
          font-size: 26px;
          font-family: 'Times New Roman', Times, serif;
          
      }
      
}


/* Layout when screen width < 500px */

@media screen and (max-width:500px)  {
      .main-nav {
          display: none;
      }
      
      .hamburger-container {
          position: absolute;
          top: 20px; 
          right: 60px; 
          z-index: 0;
          position: absolute;
       }
      
      .hamburger-menu {
          width: 30px;
          height: 20px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          cursor: pointer;
          position: absolute;
      }
          
      .line {
          width: 100%;
          height: 3px;
          background-color: #6c6c6c; 
          z-index: -1;
      }

      .message {
          height: 30vh;
      }

      .container {
          width: 100%;
          height: 100%;
          display: flex;
          flex-wrap: wrap;
          justify-content: center; 
          padding: 20px;
      }
              
      .item {
          width: 100%; 
          height: 150px;
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          box-sizing: border-box; 
          flex-basis: auto;
          margin-bottom: 16px;
          margin-left: 12px;
          padding-top: 12px;
          font-family: 'Times New Roman', Times, serif;
          background-color: #89A;
      }

      .hamburger-menu-container{
        background-color: #ffffff;
        width: 40vh;
        height: 100vh;
      }
      

      .menu-list{
          list-style: none;
          position: relative;
          left: -60px;
          top: -40px;
      }
      
      .menu-list li{
          margin: 5vh 0 ;
          font-family: 'Times New Roman', Times, serif;
          font-size: 16px;
      }
      
      .x-button{
          position: absolute;
          right: 15px;
          top: 10px;
          font-size: 26px;
          font-family: 'Times New Roman', Times, serif;
          
      }
      
}